import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
// Types



type Props = {
    option1: string,
    option2: string
};

const Sidebar: React.FC<Props> = (option1, option2) => (
    <div>
        <Stack spacing={2} direction="row">
            <Button variant="text">{option1}</Button>
            <Button variant="contained">{option2}</Button>
        </Stack>
    </div>
);

export default Sidebar;